<template>
  <div>
  <el-table
    ref="multipleTable"
    :data="tableData"
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
      label=""
      width="120">
      <template slot-scope="scope">{{ scope.row.status }}</template>
    </el-table-column>
    <el-table-column
      prop="time"
      label=""
      width="120">
    </el-table-column>
    <el-table-column
      prop="alert"
      label=""
      show-overflow-tooltip>
    </el-table-column>
  </el-table>
</div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          status: 'Inactivity',
          time: '4 days ago',
          alert: '10 days without any entries',
        }, {
          status: 'Keyword',
          time: '12 days ago',
          alert: 'Some keywords',
        }, {
          status: 'Inactivity',
          time: '4 days ago',
          alert: '10 days without any entries',
        }, {
          status: 'Keyword',
          time: '12 days ago',
          alert: 'Some keywords',
        },
        ],
        multipleSelection: []
      }
    },

    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    }
  }
</script>

<style>
  .el-table__header tr,
  .el-table__header th {
    padding: 0;
    height: 36px;
    background-color: #f1f1f1;
}
</style>


